import React from "react";
import "./Contacts.css";
import FormContact from "./FormContact";

function Contacts() {
    return (
        <section id="contacts">
            <h2>Contacts</h2>
            <div className="contact__info">
                <p>Thank's for you want contact me!</p>
                <FormContact />
            </div>
        </section>
    );
}

export default Contacts;
