import React, { useState } from "react";
import emailjs from "emailjs-com";

const App = () => {
    const frmContact = {
        user_email: "",
        user_name: "",
        concernCategory: "",
        emailTitle: "",
        message: "",
    };
    const [contact, setContact] = useState(frmContact);
    const [showMessage, setShowMessage] = useState(false);
    const handleChange = (e) => {
        const { name, value } = e.target;
        setContact({ ...contact, [name]: value });
    };
    const handleSubmit = (e) => {
        e.preventDefault();

        emailjs
            .send(
                "default_service",
                "template_6btqygn",
                contact,
                "user_MnDWWU5LWv7sGaFdZUNm4"
            )
            .then(
                (response) => {
                    console.log("SUCCESS!", response.status, response.text);
                    setContact(frmContact);
                    setShowMessage(true);
                },
                (err) => {
                    console.log("FAILED...", err);
                }
            );
    };
    return (
        <div className="container pt-2 text-center">
            {showMessage ? (
                <div
                    className="alert alert-success col-md-5 mx-auto"
                    role="alert"
                >
                    Email Send Success!!
                </div>
            ) : (
                ``
            )}

            <form onSubmit={handleSubmit}>
                <div className="pt-3">
                    <h3 className="font-weight-bold"> Contact Me !! </h3>
                </div>
                <div className="pt-3 col-md-5 mx-auto">
                    <div className="form-group text-left">
                        {" "}
                        <b>Your Email</b> <br />
                        <input
                            required
                            type="text"
                            value={contact.user_email}
                            name="user_email"
                            onChange={handleChange}
                            className="form-control"
                            placeholder="Your email"
                        />
                    </div>
                </div>
                <div className="pt-3 col-md-5 mx-auto">
                    <div className="form-group text-left">
                        {" "}
                        <b>Your Name</b> <br />
                        <input
                            required
                            type="text"
                            value={contact.user_name}
                            name="user_name"
                            onChange={handleChange}
                            className="form-control"
                            placeholder="Your name"
                        />
                    </div>
                </div>
                <div className="pt-3 col-md-5 mx-auto">
                    <div className="form-group text-left">
                        {" "}
                        <b>Concern Category</b> <br />
                        <select
                            required
                            className="form-control"
                            value={contact.concernCategory}
                            onChange={handleChange}
                            name="concernCategory"
                        >
                            <option value="">Select</option>
                            <option value="info">Info</option>
                            <option value="buy">Job</option>
                            <option value="other">Other</option>
                        </select>
                    </div>
                </div>
                <div className="pt-3 col-md-5 mx-auto">
                    <div className="form-group text-left">
                        {" "}
                        <b>Subject</b> <br />
                        <input
                            value={contact.emailTitle}
                            required
                            type="text"
                            name="emailTitle"
                            onChange={handleChange}
                            className="form-control"
                            placeholder="Subject"
                        />
                    </div>
                </div>
                <div className="pt-3 col-md-5 mx-auto">
                    <div className="form-group text-left">
                        {" "}
                        <b>Describe your concerns</b> <br />
                        <textarea
                            required
                            name="message"
                            onChange={handleChange}
                            className="form-control"
                            placeholder="Describe your concerns"
                            value={contact.message}
                        ></textarea>
                    </div>
                </div>
                <div className="pt-3 col-md-5 mx-auto text-left">
                    <button className="btn btn-primary">Send</button>
                </div>
            </form>
        </div>
    );
};
export default App;
