import React from "react";
import "./About.css";
import Technologies from "./Technologies";
import Css from "../images/css.svg";
import Html from "../images/html-5.svg";
import Rt from "../images/atom.svg";
import Js from "../images/js.svg";
import Sass from "../images/sass.svg";

function About() {
    return (
        <section id="about">
            <h2>About</h2>
            <p>
                I'm Luigi Arrieta, I'm from Montelibano, Cordoba but I living in
                La Ceja, Antioquia, I'm 19 years old and i'm passionate in web
                development, I know technoligies such HTML5, CSS3, JS, REACT.
            </p>
            <div className="technologies">
                <Technologies src={Html} alt="HTML Icono" technologie="HTML" />
                <Technologies src={Css} alt="CSS Icono" technologie="CSS" />
                <Technologies src={Sass} alt="Sass Icono" technologie="Sass" />
                <Technologies src={Js} alt="JS Icono" technologie="JS" />
                <Technologies src={Rt} alt="React Icono" technologie="React" />
            </div>
        </section>
    );
}

export default About;
